<script>
import axios from 'axios';

export default {
  name: 'Login',
  data() {
    return {
      activeMenu: 'main',
      user: { id: 0, firstname: '', email: '' },
      updateUser: { firstname: '', lastname: '', password: '' },
    };
  },
  methods: {
    async userChange() {
      try {
        let data = await axios({
          url: 'http://127.0.0.1:3000/user-email',
          method: 'put',
          contentType: 'application/json',
          data: {
            email: this.email,
            firstname: this.updateUser.firstname,
            lastname: this.updateUser.lastname,
          },
        });
        console.log(data);
        if (this.updateUser.password != '') {
          data = await axios({
            url: 'http://127.0.0.1:3000/user-password',
            method: 'put',
            contentType: 'application/json',
            data: {
              email: this.email,
              new_password: this.updateUser.password,
            },
          });
          console.log(data);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async deleteUser() {
      try {
        if (confirm('Are you sure to delete this account?')) {
          let data = await axios({
            url: 'http://127.0.0.1:3000/user',
            method: 'delete',
            contentType: 'application/json',
            data: {
              email: this.email,
            },
          });
          console.log(data);
          this.$router.push('/');
        }
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    if (localStorage.getItem('id') != null)
      this.user.id = localStorage.getItem('id');
    if (localStorage.getItem('firstname') != null)
      this.user.firstname = localStorage.getItem('firstname');
    if (localStorage.getItem('lastname') != null)
      this.user.firstname = localStorage.getItem('lastname');
    if (localStorage.getItem('email') != null)
      this.user.email = localStorage.getItem('email');
  },
};
</script>

